'use client';

import { useEffect, useRef, useState } from 'react';

type Props = {
  text: string;
};

export default function OpenButton({ text }: Props) {
  const pRef = useRef<HTMLParagraphElement>(null);
  const [isClamped, setIsClamped] = useState(false);

  useEffect(() => {
    const el = pRef.current;
    if (!el) return;

    // clamp後の表示高さより、全文の高さが大きければ「省略されている」
    setIsClamped(el.scrollHeight > el.clientHeight);
  }, [text]);

  return (
    <div>
      <p ref={pRef} className="text-sm leading-relaxed line-clamp-4 wrap-break-word">
        {text}
      </p>

      {isClamped && <span className="mt-1 inline-block text-xs text-gray-500">全文を読む</span>}
    </div>
  );
}
